.base-input {
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  color: $dark;
  box-sizing: border-box;
  background: $white;
  border-radius: 3px;
  width: 495px;
  height: 68px;
  border: 0;
  outline: 0;
  padding: 15px;
}

.form {
  .form__field {
    display: flex;
    flex-direction: column;
    font-family: "Inter";
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    color: $white;

    &:not(:first-child) {
      margin-top: 20px;
    }
  }

  .form__field__label {
    margin-bottom: 10px;
    margin-left: 13px;
  }

  .form__field__input {
    @extend .base-input;
  }

  .form__field__textarea {
    @extend .base-input;
    width: 495px;
    height: 200px;
  }
}

.field {
  display: flex;
  flex-direction: column;
}

.field__label {
  margin-bottom: 10px;
}

.textarea {
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  width: 810px;
  height: 340px;
  background: #fafafa;
  border-radius: 3px;
  outline: unset;
  padding: 30px;
  box-sizing: border-box;
}

.upload-input {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 240px;
  height: 60px;
  background: #ffffff;
  border-radius: 3px;
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 26px;
  color: #214ef8;

  &::before {
    content: "";
    width: 21px;
    height: 25px;
    margin-right: 10px;
    background-image: url("/assets/images/clip-outlined.svg");
  }

  .upload-input__file {
    display: none;
  }
}
