.about-project {
  display: flex;
  flex-direction: row;
  padding: 150px 240px 0 240px;
  height: 560px;

  .about-project__main {
    margin-top: -37px;

    .about-project__main__desc {
      margin-top: 60px;
      padding-left: 180px;
      max-width: 390px;

      .about-project__main__desc__text {
        color: $white;
        font-family: "Inter";
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 26px;
        margin: 0;
      }
    }

    .about-project__main__box {
      width: 345px;
      height: 298px;
      margin: 87px 0 0 180px;
      background-color: $light-white;
      background-color: #fefefe;
      background-image: url(/assets/images/about-project-logo.png);
      background-repeat: no-repeat;
      background-position: center center;
    }
  }

  .about_project__image {
    width: 495px;
    height: 560px;
    border-radius: 3px;
    margin-left: 215px;
  }
}
