.news-item-section {
  padding: 150px 240px 150px 240px;
}

.news-item-section__news__header {
  display: flex;
  justify-content: space-between;
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
}

.news-item-section__news__body {
  padding-top: 40px;
}

.news-item-section__news__body__title {
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
}

.news-item-section__news__body__text {
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
}

.news-item-section__news__body__image {
  width: 100%;
}
