.pagination {
  display: flex;
}

.pagination__button {
  cursor: pointer;
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  border: 0;
  border-radius: 100%;
  box-sizing: border-box;
}

.pagination__button_active {
  border: 1px solid #ffffff;
}

.pagination__button_disabled {
  border-color: 1px solid rgba(255, 255, 255, 0.4);
}

.pagination__button_navigation {
  margin-right: 10px;
  margin-left: 10px;
  color: rgba(33, 78, 248, 0.4);
  border: 1px solid #ffffff;
}

.pagination__button_left-arrow {
  &::before {
    content: "";
    background-image: url("/assets/images/arrow-left.svg");
    height: 25px;
    width: 29px;
    background-repeat: no-repeat;
  }
}
.pagination__button_right-arrow {
  &::before {
    content: "";
    background-image: url("/assets/images/arrow-left.svg");
    height: 25px;
    width: 29px;
    background-repeat: no-repeat;
    transform: rotate(180deg);
  }
}
