.finger-print-gen {
  padding: 150px 240px 0 240px;
}

.finger-print-gen__sub-title {
  display: inline-block;
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  margin-top: 60px;
}

.finger-print-gen__generator {
  margin-top: 60px;
}

.finger-print-gen__generator__area {
  padding-top: 120px;
  padding-bottom: 120px;
}

.finger-print-gen__result {
  &::before {
    display: block;
    content: "";
    height: 2px;
    border-radius: 2px;
    background-color: #214ef8;
  }
}

.finger-print-gen__result__content {
  padding: 40px 240px 120px 240px;
}

.finger-print-gen__result__content__field {
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 26px;

  &:not(:first-child) {
    margin-top: 20px;
  }
}

.finger-print-gen__result__content__field__label {
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 26px;
  margin-right: 5px;
}

.finger-print-gen__result__content__field__value {
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  line-break: anywhere;
}

.finger-print-gen__result__content__button {
  margin-top: 60px;
}
