.news-item {
  position: relative;
  cursor: pointer;
  background: linear-gradient(0deg, rgba(4, 20, 83, 0.5), rgba(4, 20, 83, 0.5));
  border: 1px solid $white;
  box-sizing: border-box;
  border-radius: 3px;
  width: 705px;
  height: 300px;

  .news-item__body {
    box-sizing: content-box;
    position: absolute;
    height: calc(100% - 40px);
    width: calc(100% - 140px);
    top: 0;
    left: 0;
    padding-top: 40px;
    padding-left: 70px;
    padding-right: 70px;
  }

  .news-item__title {
    font-family: "Inter";
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 27px;
    color: $white;
  }

  .news-item__image {
    position: relative;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: opacity 0.5s;
  }

  .news-item__desc {
    font-family: "Inter";
    color: $white;
    font-size: 18px;
  }

  .news-item__read-more {
    box-sizing: content-box;
    margin-top: 45px;
  }

  .news-item__date {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    background-color: $white;
    height: 80px;
    width: 80px;
    right: 0;
    bottom: 0;
    border-radius: 3px 0px;
    font-family: "Inter";
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    color: $dark-blue;
  }

  &:hover {
    .news-item__image {
      opacity: 0.5;
    }
  }
}
