.header {
  position: sticky;
  display: flex;
  align-items: center;
  justify-content: space-between;
  top: 0px;
  height: 80px;
  background-color: $white;
  padding: 0 240px 0 240px;
  z-index: 1;

  .header_logo-text {
    font-family: "Inter";
    font-weight: 800;
    font-size: 48px;
  }
}

.header_nav {
  .header_nav__item {
    cursor: pointer;
    font-family: "Inter";
    font-weight: 600;
    font-size: 20px;
    color: $dark;
    text-decoration: none;

    &:hover {
      opacity: 0.7;
    }

    &:not(:last-child) {
      margin-right: 35px;
    }

    &.header_nav__item_active {
      border-bottom: 2px solid $blue;
      opacity: 0.7;
      border-radius: 2px;
    }
  }
}
