.main-banner {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 700px;
  background: linear-gradient(0deg, rgba(4, 20, 83, 0.4), rgba(4, 20, 83, 0.4)),
    url("/assets/images/main_banner.png");

  .main-banner__content {
    .main-banner__title {
      margin: 153px 0 0 0;
      font-family: "Inter";
      font-weight: 900;
      font-size: 90px;
      line-height: 109px;
      color: $white;
      max-width: 862px;
      text-align: center;
    }

    .main-banner__sub-title {
      margin: 40px 0 0 0;
      font-family: "Inter";
      font-weight: 600;
      font-size: 30px;
      line-height: 36px;
      color: $white;
    }
  }
}

.main-banner__buttons {
  display: flex;
  justify-content: center;
  margin-top: 70px;
}
