.contact-section__banner {
  background: linear-gradient(0deg, rgba(4, 20, 83, 0.4), rgba(4, 20, 83, 0.4)),
    url("/assets/images/contacts_banner.png");
}

.contact-section {
  padding: 150px 240px 150px 240px;
}

.contact-section__content {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}

.contact-section__info {
  flex: 1;
  margin-right: 10px;
}

.contact-section__map {
  flex: 1;
}

.contact-section__info__title {
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 64px;
}

.contact-section__info__sub-title {
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  margin-top: 60px;
}

.contact-section__info__personal-info {
  display: flex;
  flex: 1;
}

.contact-section__info__personal {
  display: flex;
  flex-direction: column;

  &:first-child {
    margin-right: 150px;
  }
}
.contact-section__info__personal__field {
  margin-top: 20px;
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
}

.contact-section__info__personal__name {
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
}

.social-button {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  width: 40px;
  height: 40px;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: #fff;
  fill: #214ef8;
  stroke: #214ef8;

  &:hover {
    background-color: #214ef8;
    fill: #fff;
    stroke: #fff;
  }
}

.contact-section__info__personal__social {
  display: flex;

  & > :not(:first-child) {
    margin-left: 20px;
  }
}

.contact-section__info__site {
  margin-top: 20px;
}

.contact-section__info__footer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
