.news {
  padding: 150px 240px 0 240px;

  .news__header {
    display: flex;
    justify-content: space-between;

    .news__header__learn-more {
      align-self: center;
    }
  }

  .news__content {
    display: flex;
    margin-top: 60px;

    & > * {
      flex: 1;
    }

    & > *:not(:last-child) {
      margin-right: 30px;
    }
  }
}
