.button {
  width: 240px;
  height: 60px;
  background: $blue;
  border-radius: 30px;
  outline: none;
  border: 0;
  color: $white;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  cursor: pointer;
  transition: box-shadow 0.5s;

  &:hover {
    box-shadow: 0px 15px 20px rgba(0, 41, 189, 0.7);
  }
}

.button_link {
  display: inline-block;
  cursor: pointer;
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  outline: none;
  border: 0;
  background: transparent;
  color: $white;
  height: 24px;
  padding-left: 0;
  text-decoration: none;

  &::after {
    content: "";
    display: block;
    background: url("/assets/images/arrow.svg") no-repeat;
    width: 16px;
    height: 16px;
    float: right;
    margin-top: 3px;
    margin-left: 10px;
  }

  &:hover {
    border-bottom: 2px solid $blue;
    border-radius: 2px;
  }
}

.button-tabs {
  display: flex;
  min-width: 270px;
}

.button-tab {
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-family: "Inter";
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  outline: none;
  border: 0;
  background: $white;
  color: $blue;
  width: 270px;
  height: 60px;
  padding-left: 0;
  border-radius: 3px 0px 0px 3px;
  text-decoration: none;

  &.button-tab_active {
    background: $blue;
    color: $white;
  }
}

.button_submit {
  margin-top: 30px;
}
