.news-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 150px 240px 150px 240px;
}

.news-section__news {
  display: flex;
  justify-content: center;
  align-items: center;
}

.news-section__column {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &:first-child {
    margin-right: 30px;
  }

  & > *:not(:first-child) {
    margin-top: 30px;
  }
}

.news__pagination {
  align-self: flex-end;
  margin-top: 50px;
}
