.footer {
  display: flex;
  justify-content: space-between;
  height: 324px;
  color: $white;
  background-color: $dark-blue;
  padding: 0 240px 0 240px;
  padding-top: 60px;
  padding-bottom: 60px;
  box-sizing: border-box;

  .footer__column {
    flex: 1;
  }

  .footer_logo-text {
    font-family: "Inter";
    font-style: normal;
    font-weight: 800;
    font-size: 48px;
    line-height: 58px;
    text-align: center;
  }

  .footer__company {
    font-family: "Inter";
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }

  .footer_phones {
    display: flex;
    flex-direction: column;

    .footer_phones__phone {
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }

  .footer__info {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;
  }

  .footer__source {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
  }

  .footer__nav {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
  }

  .footer__nav__item {
    font-weight: 600;
    font-size: 20px;
    text-decoration: none;
    color: $white;

    &.footer__nav__item_active {
      opacity: 0.7;
    }
  }
}
